body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}.App-header{background:white;min-height:10vh;display:grid;grid-template-columns:auto auto auto;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:2rem;font-weight:500;color:#5E96AE}

.er-temperature-selection{margin-top:0.5rem}



.er-chip{color:#FFF;display:inline-block;padding:0 1.6rem;margin:0 1rem;height:2.2rem;font-size:0.9rem;line-height:2.3rem;border-radius:25px;background-color:#B29DD9;box-shadow:0 2.8px 2.2px rgba(0,0,0,0.034),0 6.7px 5.3px rgba(0,0,0,0.048),0 12.5px 10px rgba(0,0,0,0.06),0 22.3px 17.9px rgba(0,0,0,0.072),0 41.8px 33.4px rgba(0,0,0,0.086),0 100px 80px rgba(0,0,0,0.12)}.er-chip:hover{cursor:pointer}.er-no-shadow{box-shadow:0 0;font-size:1rem;font-weight:500}

h2{color:#5E96AE}.er-stations{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:1rem}@media only screen and (max-width: 600px){.er-stations{grid-template-columns:1fr 1fr;grid-gap:0.5rem}}.er-temperature-selection{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr}@media only screen and (max-width: 600px){.er-temperature-selection{grid-template-columns:1fr 1fr;grid-gap:0.5rem}}

